@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700&display=swap');

:root {
  --pink: #ff7ca3;
  --pink-dark: #e8477b;
  --gray-bg: #fff9fb;
  --shadow: 0 8px 20px rgba(0,0,0,0.08);
  --card-bg: #fff;
}

/* Í∏∞Î≥∏ ÏÑ§Ï†ï */
body {
  margin: 0;
  background: var(--gray-bg);
  font-family: "Noto Sans KR", sans-serif;
  color: #333;
  overflow-x: hidden;
}

/* üß≠ ÏÉÅÎã® ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò */
.navbar {
  position: sticky;
  top: 0;
  background: linear-gradient(90deg, #ffd6e9, #ffe6ef);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 40px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.05);
  z-index: 999;
}
.logo {
  font-size: 28px;
  font-weight: 700;
  color: var(--pink-dark);
  margin: 0;
}
.search {
  width: 40%;
  padding: 8px 14px;
  border-radius: 10px;
  border: 1px solid #ddd;
  font-size: 15px;
  transition: 0.2s;
}
.search:focus {
  border-color: var(--pink-dark);
  outline: none;
  box-shadow: 0 0 5px rgba(255, 107, 158, 0.4);
}
.cart-icon {
  background: var(--pink);
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.2s;
}
.cart-icon:hover {
  background: var(--pink-dark);
  transform: scale(1.05);
}

/* üéÄ Ïπ¥ÌÖåÍ≥†Î¶¨ Î≤ÑÌäº */
.categories {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin: 25px 0;
  flex-wrap: wrap;
}
.cat-btn {
  border: none;
  background: white;
  box-shadow: 0 3px 8px rgba(0,0,0,0.08);
  border-radius: 20px;
  padding: 10px 20px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.2s;
}
.cat-btn:hover {
  background: var(--pink);
  color: white;
}
.cat-btn.active {
  background: var(--pink-dark);
  color: white;
  box-shadow: 0 4px 12px rgba(232, 71, 123, 0.3);
}

/* üßÅ ÏÉÅÌíà Ïπ¥Îìú (Í∞ÄÎ°ú Ï†ïÎ†¨) */
.product-row {
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
  overflow-x: auto;
  gap: 40px;
  padding: 50px 60px;
  scroll-behavior: smooth;
}
.product-card {
  background: var(--card-bg);
  border-radius: 18px;
  box-shadow: var(--shadow);
  text-align: center;
  flex: 0 0 230px;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
  cursor: pointer;
}
.product-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 24px rgba(0,0,0,0.15);
}
.product-img {
  width: 200px;
  height: 240px;
  object-fit: contain;
  border-radius: 18px 18px 0 0;
  margin-top: 10px;
}
.product-info {
  padding: 10px 0;
}
.product-info h3 {
  font-size: 17px;
  margin: 10px 0 5px;
  color: #333;
}
.product-info p {
  font-size: 15px;
  color: #555;
  margin-bottom: 10px;
}
.btn-add {
  background: var(--pink);
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 10px;
  font-weight: 600;
  margin-bottom: 15px;
  cursor: pointer;
  transition: 0.2s;
}
.btn-add:hover {
  background: var(--pink-dark);
  transform: scale(1.05);
}

/* üõí Ïû•Î∞îÍµ¨Îãà Ïò§Î≤ÑÎ†àÏù¥ */
.cart-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: flex-end;
  z-index: 1000;
  animation: fadeIn 0.3s ease;
}

/* üõç Ïû•Î∞îÍµ¨Îãà ÏÇ¨Ïù¥Îìú Ìå®ÎÑê */
.cart-panel {
  position: fixed;
  right: 0;
  top: 0;
  width: 340px;
  height: 100vh;
  background: #fff;
  box-shadow: -6px 0 20px rgba(0,0,0,0.1);
  padding: 25px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  animation: slideIn 0.3s ease;
  overflow-y: auto;
}
.cart-panel h2 {
  text-align: center;
  font-size: 22px;
  color: var(--pink-dark);
  margin-bottom: 10px;
}

/* ÎπÑÏñ¥ ÏûàÏùå Î¨∏Íµ¨ */
.empty-cart {
  color: #999;
  font-weight: 500;
  text-align: right;
  font-size: 15px;
  margin-top: 60px;
  margin-right: 10px;
}

/* Ïû•Î∞îÍµ¨Îãà ÏïÑÏù¥ÌÖú */
.cart-item {
  display: grid;
  grid-template-columns: 1.5fr 1fr 1fr;
  align-items: center;
  padding: 8px 0;
  border-bottom: 1px solid #eee;
  font-size: 14px;
}
.cart-item span:first-child { text-align: left; }
.cart-item span:last-child { text-align: right; }

/* ÏàòÎüâ Ï°∞Ï†à */
.qty-control {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
}
.qty-btn {
  background: #ffe4ec;
  border: 1px solid #ffb7cb;
  color: var(--pink-dark);
  font-size: 14px;
  width: 22px;
  height: 22px;
  border-radius: 6px;
  font-weight: 700;
  cursor: pointer;
  transition: 0.2s;
}
.qty-btn:hover {
  background: var(--pink);
  color: white;
}
.qty-control span {
  width: 28px;
  text-align: center;
  font-weight: 600;
}

/* Ï¥ù Í∏àÏï° */
.cart-total {
  text-align: right;
  font-weight: 600;
  margin-top: 12px;
  font-size: 15px;
}


.checkout-btn-cart {
  position: fixed !important;
  bottom: 25px !important;
  right: 40px !important;
  background: var(--pink-dark) !important;
  color: white !important;
  border: none !important;
  padding: 10px 24px !important;
  width: auto !important;
  height: auto !important;
  border-radius: 8px !important;
  font-weight: 600 !important;
  cursor: pointer !important;
  transition: 0.2s !important;
  font-size: 14px !important;
  box-shadow: 0 4px 12px rgba(232, 71, 123, 0.3) !important;
  z-index: 1100 !important;
  display: inline-block !important;
}


/* ‚úÖ hoverÎèÑ cartÎ≤ÑÌäº Ï†ÑÏö©ÏúºÎ°ú */
.checkout-btn-cart:hover {
  background: var(--pink);
  transform: translateY(-2px);
}


/* üí≥ Í≤∞Ï†ú ÏôÑÎ£å / Ï∑®ÏÜå Î≤ÑÌäº (Í≤∞Ï†ú ÌéòÏù¥ÏßÄ ÎÇ¥ Ï†ÑÏö©) */
.checkout-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.checkout-btn, .cancel-btn {
  flex: 1;
  margin: 0 5px;
  border: none;
  border-radius: 10px;
  padding: 10px;
  font-weight: 600;
  cursor: pointer;
}
.checkout-btn {
  background: var(--pink-dark);
  color: white;
}
.checkout-btn:hover { background: var(--pink); }
.cancel-btn { background: #ccc; }
.cancel-btn:hover { background: #b3b3b3; 
}

@media (max-width: 768px) {
  .checkout-btn-cart {
    bottom: 20px;
    right: 50%;
    transform: translateX(50%);
    font-size: 15px;
    padding: 10px 30px;
    border-radius: 12px;
  }
}



/* üí≥ Í≤∞Ï†ú ÌéòÏù¥ÏßÄ */
.checkout-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(180deg, #fff0f6, #ffe6ed);
}
.checkout-card {
  background: white;
  padding: 40px;
  border-radius: 20px;
  box-shadow: var(--shadow);
  width: 450px;
}
.checkout-card h2 {
  color: var(--pink-dark);
  text-align: center;
  margin-bottom: 20px;
}
.checkout-form {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin: 20px 0;
}
.checkout-form label {
  font-weight: 600;
  color: #444;
}
.checkout-form input {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 10px;
}
.checkout-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.checkout-btn, .cancel-btn {
  flex: 1;
  margin: 0 5px;
  border: none;
  border-radius: 10px;
  padding: 10px;
  font-weight: 600;
  cursor: pointer;
}
.checkout-btn {
  background: var(--pink-dark);
  color: white;
}
.checkout-btn:hover { background: var(--pink); }
.cancel-btn { background: #ccc; }
.cancel-btn:hover { background: #b3b3b3; }

/* ‚úÖ Í≤∞Ï†ú ÏôÑÎ£å */
.complete-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(180deg, #fff5f9, #ffdbe6);
}
.complete-card {
  background: #fff;
  padding: 50px;
  border-radius: 20px;
  box-shadow: var(--shadow);
  text-align: center;
  width: 400px;
  animation: fadeIn 0.4s ease;
}
.complete-card h1 { color: var(--pink-dark); }
.complete-img {
  width: 160px;
  height: 160px;
  object-fit: contain;
  border-radius: 10px;
  margin: 20px 0;
}
.order-total {
  font-size: 17px;
  color: #444;
}
.btn-back {
  background: var(--pink);
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  margin-top: 20px;
}
.btn-back:hover { background: var(--pink-dark); }

/* ü™Ñ ÏÉÅÏÑ∏Î≥¥Í∏∞ Î™®Îã¨ */
.modal {
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: rgba(0,0,0,0.45);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}
.modal-box {
  background: white;
  padding: 30px;
  border-radius: 16px;
  text-align: center;
  width: 350px;
  box-shadow: var(--shadow);
  animation: fadeIn 0.3s ease;
}
.modal-box img {
  width: 180px;
  height: 180px;
  object-fit: contain;
  border-radius: 12px;
}
@keyframes fadeIn {
  from { opacity: 0; transform: scale(0.95); }
  to { opacity: 1; transform: scale(1); }
}

/* ‚öôÔ∏è Ìë∏ÌÑ∞ */
.footer {
  text-align: center;
  padding: 40px 0;
  background: #fff;
  color: #888;
  font-size: 14px;
  margin-top: 60px;
  box-shadow: 0 -3px 10px rgba(0,0,0,0.05);
}

/* üì± Î∞òÏùëÌòï */
@media (max-width: 768px) {
  .navbar { flex-direction: column; gap: 10px; }
  .search { width: 80%; }
  .product-row { flex-wrap: wrap; justify-content: center; }
  .product-card { flex: 0 0 45%; margin-bottom: 20px; }
  .cart-panel { width: 100%; height: 60vh; bottom: 0; top: auto; border-radius: 16px 16px 0 0; }
}
